import React, { useState } from 'react'
import { AnchorLink } from 'gatsby-plugin-anchor-links'
import { Link } from 'gatsby'

const Header = () => {
  //defalt classes for hamburger menu
  const [HamburgerMenuClassList, setHamburgerMenuClassList] = useState(
    'relative h-8 w-8 cursor-pointer text-3xl md:hidden'
  )

  // default classes for mobile-menu div (including animation)
  const [MobileMenuClassList, setMobileMenuClassList] = useState(
    'top-68 justify-content-center absolute hidden w-full origin-top animate-open-menu flex-col bg-black text-5xl'
  )

  // toggle state of hamburger and mobile menu div onClick
  const toggleClick = () => {
    if (HamburgerMenuClassList.includes('toggle-btn')) {
      setHamburgerMenuClassList(
        HamburgerMenuClassList.replace('toggle-btn', '')
      )
    } else {
      setHamburgerMenuClassList(HamburgerMenuClassList + ' toggle-btn')
    }

    if (MobileMenuClassList.includes('hidden')) {
      setMobileMenuClassList(MobileMenuClassList.replace('hidden', 'flex'))
    } else {
      setMobileMenuClassList(MobileMenuClassList.replace('flex', 'hidden'))
    }
  }

  // return header function
  return (
    <header className='sticky top-0 z-10 bg-orange-200 text-slate-700 sm:mb-4'>
      <section className='mx-auto flex max-w-4xl items-center justify-between p-4'>
        <h1 className='text-3xl font-medium'>
          <Link to='/' title='Home Page'>
            ✒️Ankeet Patel
          </Link>
        </h1>
        <div>
          <button
            id='hamburger-button'
            className={HamburgerMenuClassList}
            onClick={toggleClick}
          >
            {/* <!-- &#9776; --> */}
            <div className="absolute top-4 -mt-0.5 h-1 w-8 rounded bg-white transition-all duration-500 before:absolute before:h-1 before:w-8 before:-translate-x-4 before:-translate-y-3 before:rounded before:bg-white before:transition-all before:duration-500 before:content-[''] after:absolute after:h-1 after:w-8 after:-translate-x-4 after:translate-y-3 after:rounded after:bg-white after:transition-all after:duration-500 after:content-['']"></div>
          </button>
          <nav className='hidden space-x-8 text-xl md:block' aria-label='main'>
            <AnchorLink
              to='/#projects'
              title='My Projects'
              className='hover:opacity-80'
            >
              Projects
            </AnchorLink>
            <AnchorLink
              to='/#skills'
              title='Skills Section'
              className='hover:opacity-80'
            >
              Skills
            </AnchorLink>
            <Link
              to='/about'
              title='Learn more about me'
              className='hover:opacity-80'
            >
              About Me
            </Link>
            <AnchorLink
              to='/#contact'
              title='Contact Me'
              className='hover:opacity-80'
            >
              Contact
            </AnchorLink>
          </nav>
        </div>
      </section>
      {/* <!-- Mobile Menu --> */}
      <section
        id='mobile-menu'
        className={MobileMenuClassList}
        onClick={toggleClick}
      >
        {/* <!-- <button className="text-8xl self-end px-6">
              &times;
          </button> --> */}
        <nav
          className='flex min-h-screen flex-col items-center py-8'
          aria-label='mobile'
        >
          <AnchorLink
            to='/#hero'
            className='w-full py-6 text-center hover:opacity-90'
          >
            Home
          </AnchorLink>
          <AnchorLink
            to='/#projects'
            className='w-full py-6 text-center hover:opacity-90'
          >
            Projects
          </AnchorLink>
          <AnchorLink
            to='/#skills'
            className='w-full py-6 text-center hover:opacity-90'
          >
            Skills
          </AnchorLink>
          <Link
            to='/about'
            className='w-full py-6 text-center hover:opacity-90'
          >
            About Me
          </Link>
          <AnchorLink
            to='/#contact'
            className='w-full py-6 text-center hover:opacity-90'
          >
            Contact Me
          </AnchorLink>
        </nav>
      </section>
    </header>
  )
}

export default Header
